import Vue from 'vue'
import Router from 'vue-router'
import Table from '@/components/Table.vue'
import News from '@/components/News.vue'
import Home from '@/components/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
		path: "/table",
		name: "table",
		component: Table
    },
	{
		path: "/news",
		name: "news",
		component: News
    },
	{
		path: "/home",
		name: "home",
		component: Home
    },
	{
		path: "*",
		name: "home",
		component: Home
    }
  ]
})



