<template>
    <div class="container">		
		<table class="table table-striped table-bordered table-hover table-responsive">
			<thead>
			<tr>
				<th> Company </th>
				<th> Market Cap ($)</th>
				<th> P/E Ratio </th>
				<th> Day Change (%) </th>
				<th> 52 Week High </th>
				<th> 52 Week Low </th>
				<th> 1 Year Change (%)</th>
				<th> Average Total Volume</th>
			</tr>
			</thead>
			<tbody>
				<tr v-for="(result, index) in  results">
					<td> 
						{{ result.companyName }}
					</td>
					<td> 
						{{ result.marketCap | round }}
					</td>
					<td> 
						{{ result.peRatio }}
					</td>
					<td> 
						{{ result.changePercent | round }}
					</td>
					<td> 
						{{ result.week52High }}
					</td>
					<td> 
						{{ result.week52Low }}
					</td>
					<td> 
						{{ result.ytdChange | round }}
					</td>
					<td> 
						{{ result.avgTotalVolume | round }}
					</td>
				</tr>
				<tr class="search_row" v-for="result in  searchResults">
					<td> 
						{{ result.companyName }}
					</td>
					<td> 
						{{ result.marketCap | round }}
					</td>
					<td> 
						{{ result.peRatio }}
					</td>
					<td> 
						{{ result.changePercent | round }}
					</td>
					<td> 
						{{ result.week52High }}
					</td>
					<td> 
						{{ result.week52Low }}
					</td>
					<td> 
						{{ result.ytdChange | round}}
					</td>
					<td> 
						{{ result.avgTotalVolume | round }}
					</td> 
						
				</tr>
			</tbody>
		</table>
		<br>
		<div class="search_box">
			<input v-model="searchQuery" 
			v-on:keyup.enter="search" 
			placeholder="Search stocks" id="search_input">
		</div>
		<div class="button_container">
			<button class="btn btn-primary" @click="search()">Search</button>
		</div>
		
	</div>
</div>		
</template>

<script>
import axios from 'axios';

export default {
	name: "table",
	data() {
		return {
			results: [],
			searchQuery: "",
			searchResults: {}
		}
	},
	methods: {
		loadData() {
			const tickers = ["TSLA", "AMZN", "SHOP", "CSCO", "AAPL", "FSLR",
			"BRK.B", "BABA", "JD.CH", "NEE", "SHAK", "SEDG"];
			let vm = this;
			for(let i=0; i<tickers.length; i++) {
			let url = "https://sandbox.iexapis.com/stable/stock/"+tickers[i]+"/quote?token=Tpk_4c7d41fa57104aec8bc22e72fff7b1c4"
				axios.get(url)
					.then((response) => {
						vm.results = vm.results.concat(response.data);
				});
			}
		},
		
		search() {
			const searchUrl = "https://sandbox.iexapis.com/stable/stock/"+this.searchQuery+"/quote?token=Tpk_4c7d41fa57104aec8bc22e72fff7b1c4";
			axios.get(searchUrl) 
				.then((response) => {
					this.searchResults = [response.data];
					console.log(response.data)
				})
				.catch((err) => {
					alert("Sorry, that stock ticker is not valid. Please try another one")
				});	
		}
	},
	beforeMount() {
		this.loadData();
	},
	filters: {
	round(amount) {
		const amt = Number(amount)
		return amt && amt.toLocaleString(undefined, {maximumFractionDigits:2})
	}
	}	
}

</script>

<style scoped>
.container {
	margin-bottom: 5px;
}

#search_input {
	border: 1px solid blue;
	border-radius: 2px;
	outline:none !important;
	padding: 2px;

}

th {
	text-align: center !important;
}

.search_box {
	width: 50%;
	margin: auto;
	text-align: center;
}

.button_container {
	text-align: center;
	margin-top: 4px;
}

.search_row {
	background-color: #67B9BC !important;
}
</style>


