<template>
	<div id="app">
		</br>
		<div class="news_search_box">
			<input type="text" 
				@keyup.enter="newsSearch()" 
				class="news_search"
				v-model="searchQuery"
				placeholder="Please Enter A Stock Ticker To Search Here..."
			/>
		</div>	
		<div class="button_container">
			<button class="btn btn-primary" @click="search()">Search</button>
		</div>
		<table class="news_table">
			<tr class="news_box" v-for="result in  searchResults">
				<td>
					<img class="news_img" :src="result.image_url" :alt="result.text"/>
					<a target="blank" 
						:href="result.news_url" 
					> 
						<p class="news_text">
						{{ result.title }}
						</p>
					</a>
				</td>		
			</tr>				
		</table>
		<hr>
		<h3> Latest Business News</h3>
		
		<table class="news_table">
			<tr class="news_box" 
				v-for="article in articles"
				
			>
				<td>
					<img class="news_img" :src="article.image_url" :alt="article.text"/>
					<a target="blank" 
						:href="article.news_url" 
					> 
						<p class="news_text">
							{{ article.title }}
						</p>
					</a>
				</td>
			</tr>
		</table>
		<div class="citation_container">
			<cite>Powered by
				<a href="https://stocknewsapi.com/" target="blank">
					StocknewsAPI
				</a>
			</cite>
		</div>
	</div>
</template>

<script>
import axios from 'axios';

export default {
	data() {
		return {
			articles: {},
			searchResults: {},
			searchQuery: ''
		}
	},
	methods: {
		getNews() {
			const url = "https://stocknewsapi.com/api/v1/category?section=general&items=9&token=jm0oemvz0gejiwmk8j3irbeinjqzhyrabsyzfhqz";
			axios.get(url) 
				.then((response) => {
					this.articles = response.data.data;
					console.log(this.articles);
			});
		},
		newsSearch() {
			const url = "https://stocknewsapi.com/api/v1?tickers="+ this.searchQuery +"&items=9&fallback=true&token=jm0oemvz0gejiwmk8j3irbeinjqzhyrabsyzfhqz";
			axios.get(url) 
				.then((response) => {
					this.searchResults = response.data.data;
				})
				.catch((err) => {
					alert("Sorry, that's not a vlid stock ticker.")
				});
			}
		},
		beforeMount() {
			this.getNews();
		}
	}


</script>

<style scoped>
#app {
	margin-bottom: 10px !important;
}

.news_search_box {
	width: 50%;
	margin: auto;
	text-align: center;

}

.news_search {
	border: 1px solid blue;
	border-radius: 2px;
	outline:none !important;
	padding: 2px;
	width: 85%;
}

.button_container {
	text-align: center;
	margin-top: 4px;
}

.news_table {
	margin-left: 4% ;
	
}

.news_box {
	width: 32%;
	float: left;
	padding: 3px;
	height: 220px;
	background-color: #E9F3FF;
	margin: 3px;
}



.news_img {
	padding: 2px;
	height: 40%;
	width: 100%;
	margin: auto;
}

.news_text {
	text-align: justify;
	text-align-last: center;
	padding: 3px;
	font-weight: bold;
}


</style>





